import React from 'react';
import classes from './Regulations.module.css'
import header from '../../assets/Pictures/headRed.png'
import bigGena from '../../assets/Pictures/bigGena.png'
import bigBeer from '../../assets/Pictures/bigBeer.png'
import cn from "classnames";
import AOS from 'aos'
import 'aos/dist/aos.css';


export const Regulations = (props) => {


    AOS.init();

    return (<div className={cn({
        [classes.allDay]: !props.theme === true
    }, classes.all)}>


        <img alt={'logofull'} src={header} className={classes.jpg} data-aos="fade-down"/>

        <div className={classes.info}>
            <img alt={'logofull'} src={bigGena} className={classes.gen}/>

            <div className={classes.pravila}>
                <p data-aos="zoom-in"><span className={classes.light}>The Beerternational 2022 </span>  
                      <span className={classes.light}> GenaFond </span></p>

                <p className={classes.reglament} data-aos="zoom-in">  </p>
                <p data-aos="zoom-in">
                    : <span className={classes.light}> Counter-Strike: Global Offensive </span> <br/>
                    : <span className={classes.light}>  Short Dust 2<br/></span>
                     : <span className={classes.light}> 5</span> <br/>
                     : <span className={classes.light}> 20</span><br/>
                    : <span className={classes.light}> </span><br/>
                </p>
                <p data-aos="zoom-in">
                        .     <br/>
                     ,   .    <br/>
                       0,5  ,    <br/>
                     .       <br/>
                       -    <br/>
                     .    :    <br/>
                    <span className={classes.light}> 3 . </span> <br/>
                </p>
                <p data-aos="zoom-in">
                    <span className={classes.light}>  : </span><br/>
                          3 <br/>
                      <br/>
                     <br/>
                     <br/>
                </p>
                <p data-aos="zoom-in">
                        -  . <br/>
                              - <br/>
                    <span className={classes.light}>     .</span>    -
                     <br/>
                    .
                </p>
                <p data-aos="zoom-in">
                       :<br/>
                    <span className={classes.light}>     </span><br/>
                    <span className={classes.light}>    </span><br/>
                    <span className={classes.light}>  </span><br/>
                </p>
            </div>
            <img alt={'logofull'} src={bigBeer} className={classes.beer}/>
        </div>

    </div>);
}

